import scenarios from '../../api/scenarios'

const state = () => ({
  all: {},
  one: {
    id: 1,
    title: 'Czy druk 3D ma przyszłość?',
    factors: [
        {
            id: '1',
            title: 'Pierwszy czynnik z długą nazwą bardzo',
            ord: 1,
            key_factor: '0',
            type: '1'
        },
        {
          id: '1',
          title: 'Pierwszy czynnik z długą nazwą bardzo',
          ord: 1,
          key_factor: '0',
          type: '1'
      },
    ],
    scenarios: [
        {
            id: 1,  
            title: 'Powrót do natury',
            subtitle: '',
            ord: 1
        }
    ]
  },
  loading: false,
  editDialog: false,
  editDialogParam: undefined
})

const getters = {
  all(state) {
    return state.all
  },
  factorsByType: (state) => (type) => {
    return state.one.factors.filter((el) => el.type == type)
  },
  keyFactors: (state) => {
    return state.one.factors.filter((el) => el.key_factor == '1')
  },
  paramValue: (state) => (id) => {
    return state.all[id]
  },
  paramConfig: (state) => (id) => {
    return state.config[id] || {}
  },
  loading(state) {
    return state.loading
  },
  editDialog(state) {
    return state.editDialog
  },
  editDialogParam(state) {
    return state.editDialogParam
  }
}

const mutations = {
  insertFactor: (state, data) => {
    state.one.factors.push(data)
  },
  setAll: (state, data) => {
    state.all = data
  },
  setParameter: (state, {name, value}) => {
    state.all[name] = value
  },
  setConfig: (state, data) => {
    state.config = data
  },
  setLoading: (state, data) => {
    state.loading = data
  },
  setListener: (state, interval) => {
    state.listener = interval
  },
  editDialogOpen: (state, param) => {
    state.editDialogParam = param;
    state.editDialog = true;
  },
  editDialogClose: (state) => {
    state.editDialog = false
  },
  test: (state) => {
    console.log(state.one)
  }
}

const actions = {
  updateFactor({commit}, factor) {
    return scenarios.updateFactor(factor)
    .then(data => {
      commit('test', data);
      console.log(data);
      //commit('setLoading', false, { root: true });
      return data;
    })
  },
  /*getOne({state, commit}) {
    //commit('setLoading', true, { root: true });
    return parameters.getAll(state.currentDevice)
    .then(data => {
      commit('setAll', data);
      //commit('setLoading', false, { root: true });
      return data;
    })
  },
  startListener({commit, state, dispatch}) {
    if (state.listener === undefined) {
      commit('setListener', setInterval(() => {
        dispatch('getAll')
      }, 60000))
    }
  },
  update({state, commit}, {paramName, paramValue}) {
    commit('setLoading', true);
    return parameters.update(state.currentDevice, paramName, paramValue)
    .then(data => {
      //if (!data.error) { dispatch('getAll'); }
      commit('setLoading', false);
      return data;
    })
  },*/
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}