import { getToken } from "./getToken";
import { badResponse } from "./badResponse";

const apiUrl = 'http://localhost/api';

export default {
  getAll(controllerId) {
    const formData = new FormData();
    formData.append('controllerId', controllerId);
    formData.append('token', getToken());
    return fetch('https://pwa-backend.bornico.pl/api/sterownik/dane?t=' + new Date().getTime(), {
      method: 'POST',
      body: formData
    })
    .then(response => response.json())
    .then(data => {
      return data;
    })
    .catch(() => badResponse());
  },
  getConfig(controllerId) {
    const formData = new FormData();
    formData.append('controllerId', controllerId);
    formData.append('token', getToken());
    return fetch('https://pwa-backend.bornico.pl/api/sterownik/config?t=' + new Date().getTime(), {
      method: 'POST',
      body: formData
    })
    .then(response => response.json())
    .then(data => {
      return data;
    })
    .catch(() => badResponse());
  },
  updateFactor(factor) {
    const formData = new FormData();
    formData.append('id', factor.id);
    formData.append('title', factor.title);
    formData.append('ord', factor.ord);
    
    return fetch(apiUrl + '/update_factor?t=' + new Date().getTime(), {
      method: 'POST',
      body: formData
    })
    .then(response => response.json())
    .catch(() => badResponse());
  },
}
