import Vue from 'vue'
import VueRouter from 'vue-router'
import Diagram from '../views/Diagram.vue'
import Scenariusze from '../views/Scenariusze.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Diagram',
    component: Diagram
  },
  {
    path: '/scenariusze',
    name: 'Scenariusze',
    component: Scenariusze
  }
]

const router = new VueRouter({
  routes
})

export default router
