<template>
  <div class="w-100">
    <section class="w-100">
      <v-carousel v-model="currentSlide" height="120" light :show-arrows="false" touchless hide-delimiters>
        <v-carousel-item
          v-for="(step, i) in steps"
          :key="i"
        >
          <v-sheet
            height="100%"
            tile
          >
            <div class="w-100 step-line relative">
              <div class="center w-100 w-60-l tl">
                <div class="dib bg-white ph3 relative z-1">
                  <div class="flex items-center">
                    <v-avatar left :color="step.color" size="36">
                      <span class="white--text headline">{{ i + 1 }}</span>
                    </v-avatar> 
                    <h3 class="pl3">{{ step.title }}</h3>
                  </div>
                </div>
              </div>
            </div>
            <div class="w-100">
              <div class="center w-100 w-60-l tl ph3 pt3">
                {{ step.subtitle }}
              </div>
            </div>
          </v-sheet>
        </v-carousel-item>
      </v-carousel>
    </section>

    <section class="diagram">
      <div class="section-wrap center ph3 w-100 w-90-l">
        
        <!-- czynniki kluczowe / sily napedowe -->
        <div class="flex flex-wrap w-100">

          <div class="w-50">
            <div class="w-100 flex flex-wrap">
          
              <div class="w-third flex flex-column justify-center di-le">
                <div class="chip-wrap" v-for="(item, i) in keyFactors" :key="i" :class="[nodeClass(i, keyFactorsLength)]">
                  <v-chip
                    label
                    link
                    :color="steps[1].color"
                    text-color="white"
                  >{{ item.title }}</v-chip>
                </div>
              </div>
              <div class="w-2"><!--currentStep > 1--></div>
              <div class="w-third flex items-end">
                <div class="w-100 pv7 relative z-1" :class="{ 'di-step-2': keyFactorsLength > 0 }">
                  <v-hover v-slot="{ hover }">
                    <v-btn block height="64" 
                      :elevation="hover ? 10 : (currentStep > 1 ? 2 : 0)" 
                      :outlined="(currentStep > 1) ? false : true"
                      :color="steps[1].color"
                      :class="{'white--text': currentStep > 1 }"
                      :disabled="currentStep < 2"
                      @click="addFactor(2)"
                    >
                      {{ steps[1].title }}
                    </v-btn>
                  </v-hover>
                  <v-btn
                    fab
                    rounded
                    text
                    elevation="0"
                    class="indicator-2"
                  >2</v-btn>
                  <div class="triangle-down"></div>
                </div>
              </div>
              <div class="w-2"></div>

            </div>
          </div>

          <div class="w-50">
            <div class="w-100 flex flex-wrap">

              <div class="w-2"></div>
              <div class="w-third flex items-end">
                <div class="w-100 pv7 relative z-1" :class="{ 'di-step-3': forcesLength > 0 }">
                  <v-hover v-slot="{ hover }">
                    <v-btn block height="64" 
                      :elevation="hover ? 10 : (currentStep > 2 ? 2 : 0)"
                      :outlined="(currentStep > 2) ? false : true"
                      :color="steps[2].color"
                      :class="{'white--text': currentStep > 2 }"
                      :disabled="currentStep < 3"
                      @click="addFactor(3)"
                    >
                      {{ steps[2].title }}
                    </v-btn>
                  </v-hover>
                  <v-btn
                    fab
                    rounded
                    text
                    elevation="0"
                    class="indicator-3"
                  >3</v-btn>
                  <div class="triangle-down"></div>
                </div>
              </div>
              <div class="w-2"></div>
              <div class="w-third flex flex-column justify-center di-r">
                <div class="chip-wrap"  v-for="(item, i) in forces" :key="i" :class="[nodeClass(i, forcesLength)]">
                  <v-chip
                    label
                    link
                    :color="steps[2].color"
                    text-color="white"
                  >{{ item.title }}</v-chip>
                </div>
              </div>

            </div>
          </div>

        </div>

        <!-- scenariusze -->
        <div class="flex flex-wrap w-100">
          <div class="w-2"></div>
          <div class="w-1"></div>
          <div class="w-2"></div>
          <div class="w-2 relative di-step-5">
            <v-hover v-slot="{ hover }">
              <v-btn block height="64" class="z-1"
                :elevation="hover ? 10 : (currentStep > 2 ? 2 : 0)" 
                :outlined="(currentStep > 2) ? false : true"
                :color="steps[4].color"
                :class="{'white--text': currentStep > 2 }"
                :disabled="currentStep < 3"
              >
                Scenariusze
              </v-btn>
            </v-hover>
            <v-btn
              fab
              rounded
              text
              elevation="0"
              class="indicator-5"
            >5</v-btn>
          </div>
          <div class="w-2"></div>
          <div class="w-1"></div>
          <div class="w-2"></div>
        </div>

        <!-- czynniki / wildcards -->
        <div class="flex flex-wrap w-100">
          
          <div class="w-50">
            <div class="w-100 flex flex-wrap">
            
              <div class="w-third flex flex-column justify-center di-le">
                
                <div class="chip-wrap" v-for="(item, i) in factors" :key="i" :class="[nodeClass(i, factorsLength)]">
                  <v-chip
                    label
                    link
                    :color="steps[0].color"
                    text-color="white"
                  >{{ item.title }}</v-chip>
                </div>

              </div>

              <div class="w-2"></div>
              <div class="w-third flex items-start">
                <div class="w-100 pv7 relative" :class="{ 'di-step-1': factorsLength > 0 }">
                  <v-hover v-slot="{ hover }">
                    <v-btn block height="64" 
                      :elevation="hover ? 8 : 2"
                      :color="steps[0].color"
                      @click="addFactor(1)"
                      class="white--text">
                      {{ steps[0].title }}
                    </v-btn>
                  </v-hover>

                  <v-btn
                    fab
                    rounded
                    elevation="0"
                    class="indicator-1"
                  >1</v-btn>
                  <div class="triangle-up"></div>
                </div>
              </div>

              <div class="w-2"></div>

            </div>
          </div>


          <div class="w-50">
            <div class="w-100 flex flex-wrap">
              <div class="w-2"></div>

              <div class="w-third flex items-start">
                <div class="w-100 pv7 relative" :class="{ 'di-step-4': wildcardsLength > 0 }">
                  <v-hover v-slot="{ hover }">
                    <v-btn block height="64" 
                      :elevation="hover ? 10 : (currentStep > 2 ? 2 : 0)"
                      :outlined="(currentStep > 2) ? false : true"
                      :color="steps[3].color"
                      :class="{'white--text': currentStep > 2 }"
                      :disabled="currentStep < 3"
                      @click="addFactor(4)"
                    >
                      {{ steps[3].title }}
                    </v-btn>
                  </v-hover>
                  <v-btn
                    fab
                    rounded
                    text
                    elevation="0"
                    class="indicator-4"
                  >4</v-btn>
                  <div class="triangle-up"></div>
                </div>
              </div>

              <div class="w-2"></div>
              
              <div class="w-third flex flex-column justify-center di-r">
                <div class="chip-wrap" v-for="(item, i) in wildcards" :key="i" :class="[nodeClass(i, wildcardsLength)]">
                  <v-chip
                    label
                    link
                    :color="steps[3].color"
                    text-color="white"
                  >{{ item.title }}</v-chip>
                </div>
              </div>

            </div>
          </div>

        </div>

      </div>
    </section>

    <v-dialog
      v-model="dialog"
      max-width="330"
    >
      <v-card :color="(editedFactor.type) ? steps[editedFactor.type - 1].color : null">
        <v-card-title class="headline">
          
        </v-card-title>

        <v-card-text>
          <v-textarea
            model="editedFactor.title"
            name="input-7-1"
            :label="(editedFactor.label) ? editedFactor.label : null"
            value=""
            rows="3"
            hide-details
            dark
          ></v-textarea>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="white"
            text
            @click="dialog = false"
          >
            Anuluj
          </v-btn>

          <v-btn
            color="white"
            text
            @click="insertFactor"
          >
            Zapisz
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
// @ is an alias to /src
//import HelloWorld from '@/components/HelloWorld.vue'
import { mapGetters, mapActions } from 'vuex'

export default {
    data: () => ({
      dialog: false,
      editedFactor: {},
      steps: [
        {
          title: 'Czynniki',
          subtitle: 'Dodaj czynniki wpływające na zagadnienie. Oddziałujące zarówno w sposób negatywny, jak i pozytywny.',
          color: '#FFCC33'
        },
        {
          title: 'Czynniki kluczowe',
          subtitle: 'Spośród czynników, wybierz dwa czynniki kluczowe, które mają największy wpływ na zagadnienie.',
          color: '#FF657F'
        },
        {
          title: 'Siły napędowe',
          subtitle: 'Dodaj siły napędowe charkteryzujące się największą niepewnością i wpływem na zagadnienie.',
          color: '#55CEBD'
        },
        {
          title: 'Wild cards',
          subtitle: 'Dodaj dziekie karty charakteryzujące się niskim prawdopodobieństwem i dużym wpływem na zagadnienie.',
          color: '#54A8FE'
        },
        {
          title: 'Scenariusze',
          subtitle: 'Przejdź do scenariuszy...',
          color: '#787773'
        }
      ],
    }),
    computed: {
      ...mapGetters('scenarios', ['factorsByType', 'keyFactors']),
      factors() {
        return this.factorsByType(1)
      },
      factorsLength() {
        return this.factors.length
      },
      keyFactorsLength() {
        return this.keyFactors.length
      },
      forces() {
        return this.factorsByType(3)
      },
      forcesLength() {
        return this.forces.length
      },
      wildcards() {
        return this.factorsByType(4)
      },
      wildcardsLength() {
        return this.wildcards.length
      },
      currentStep() {
        if (this.keyFactorsLength == 2) {
          return 3
        } else if ( this.factorsLength > 1) {
          return 2
        } else {
          return 1
        }
      },
      currentSlide() {
        return this.currentStep - 1
      }
    },
    mounted() {
      console.log(this.factorsLength);
    },
    methods: {
      ...mapActions('scenarios', ['updateFactor']),
      nodeClass(i, all) {
        if (i == 0 && all == 1) {
          return { 'di-single': true }
        } else if (i == 0) {
          return { 'di-first': true }
        } else if (i == all - 1) {
          return { 'di-last': true }
        } else {
          return { 'di-mid': true }
        }
        
      },
      addFactor(type) {
        var label = 'Czynnik';

        this.editedFactor = {
          id: undefined,
          title: '',
          type: type,
          label: label
        }
        this.dialog = true;
      },
      insertFactor() {
        this.updateFactor(this.editedFactor)
        .then(() => this.dialog = false );
      }
    }
  }
</script>
